/* Change colors in this file to style to your needs on a global level. 
Certain colors are used for different and multiply types of elements. 
For greater control over colors visit each components .scss file. */
.profile-image {
  border-radius: 50%;
  border: 0.5rem solid #6c63ff;
  margin-left: auto;
  width: 100%;
  max-width: 350px;
  height: auto;
  box-shadow: rgba(0, 0, 0, 0.1) 0 30px 30px -30px;
  transition: all 0.3s ease-out; }

.profile-image:hover {
  box-shadow: none; }

.prof-title {
  margin: 0;
  font-size: 40px;
  font-weight: 400; }

.location-div {
  text-shadow: #ffebcd; }

.opp-div {
  padding-bottom: 1rem; }

.row {
  display: flex; }

.main-content-profile {
  flex: 80%; }

.image-content-profile {
  text-align: right;
  flex: 20%;
  max-width: 100%;
  height: auto; }

.bio-text {
  line-height: 30px;
  font-size: 22px; }

.desc-prof {
  line-height: 45px;
  font-size: 19px; }

.subTitle {
  color: #868e96; }

@media (max-width: 768px) {
  .row {
    display: flex;
    flex-direction: column; }
  .main-content-profile {
    text-align: center;
    order: 2; }
  .bio-text {
    font-size: 16px; }
  .image-content-profile {
    margin-left: auto;
    margin-right: auto;
    order: 1; }
  .desc-prof {
    line-height: 30px;
    font-size: 16px; }
  .profile-image {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 250px;
    height: auto; }
  .profile-image:hover {
    box-shadow: rgba(0, 0, 0, 0.1) 0 50px 50px -50px;
    transition: 0.3s ease-out; }
  .prof-title {
    padding-bottom: 1rem;
    font-size: 32px;
    text-align: center; } }
